class Shape {
    protected name: string = "";
    protected imageURL: string = "";
    setShape(name: string, imageURL: string) {
        this.name = name;
        this.imageURL = imageURL;
    }
}

class Square extends Shape {
    constructor(a: string) {
        super();
        this.setShape("Square", a);
    }
}

class Rectangle extends Shape {
    constructor() {
        super();
        this.setShape("Rectangle", "a");
    }
}

class Circle extends Shape {
    constructor() {
        super();
        this.setShape("Circle", "a");
    }
}

class ElementType {
    private x: number;
    private y: number;
    protected shape!: Shape;
    protected speed: number;
    protected size: number;
    constructor() {
        this.x = 0;
        this.y = 0;
        this.speed = 0;
        this.size = 0;
    }

    protected setElementPosition(x: number, y: number) {
        this.x = x;
        this.y = y;
    }

    protected getElementPosition() {
        return { x: this.x, y: this.y };
    }

    public move(x: number, y: number) {}

    protected getRandom() {
        return Math.random();
    }
}

export class Chase extends ElementType {
    constructor() {
        super();
        this.speed = RandomNumber.getRandomSpeed();
        this.size = RandomNumber.getRandomSize();
        this.shape = new Square(
            "C://Users//Demo//Desktop//mouseRace//media//square.svg"
        );
    }
    public move(x: number, y: number): void {
        const position = this.getElementPosition();
        if (x > position.x && y > position.y) {
            this.setElementPosition(++position.x, ++position.y);
        } else if (x < position.x && y > position.y) {
            this.setElementPosition(--position.x, ++position.y);
        } else if (x > position.x && y < position.y) {
            this.setElementPosition(++position.x, --position.y);
        } else if (x < position.x && y < position.y) {
            this.setElementPosition(--position.x, --position.y);
        } else if (x > position.x) {
            this.setElementPosition(--position.x, position.y);
        } else if (x < position.x) {
            this.setElementPosition(++position.x, position.y);
        } else if (y > position.y) {
            this.setElementPosition(position.x, --position.y);
        } else if (y < position.y) {
            this.setElementPosition(position.x, ++position.y);
        } else {
            this.onHitTarget();
        }
    }
    protected getRandom(): number {
        return 1;
    }

    private onHitTarget() {
        console.log("game over");
    }
}

class Escape extends ElementType {
    public move(x: number, y: number): void {
        const position = this.getElementPosition();
        if (x > position.x && y > position.y) {
            this.setElementPosition(--position.x, --position.y);
        } else if (x < position.x && y > position.y) {
            this.setElementPosition(++position.x, --position.y);
        } else if (x > position.x && y > position.y) {
            this.setElementPosition(--position.x, ++position.y);
        } else if (x < position.x && y < position.y) {
            this.setElementPosition(++position.x, ++position.y);
        }
    }
}

class Random extends ElementType {
    private directionX = 0;
    private directionY = 0;

    constructor() {
        super();
        this.setRandomDirection();
    }

    public checkScreenEnd(x: number, y: number): void {
        const position = this.getElementPosition();
        if (position.x === x && position.y === y) {
            this.setRandomDirection();
        }
    }

    public move(): void {
        const position = this.getElementPosition();
        this.setElementPosition(
            position.x + this.directionX,
            position.y + this.directionY
        );
    }

    private setRandomDirection(): void {
        this.directionX = this.getRandom();
        this.directionY = this.getRandom();
    }

    protected getRandom(): number {
        return RandomNumber.getRandomDirection();
    }
}

class RandomNumber {
    static getRandomSpeed() {
        return Math.random();
    }
    static getRandomSize() {
        return Math.random();
    }
    static getRandomDirection() {
        return Math.random();
    }
}

export class ArrowPosition {
    private x: number;
    private y: number;

    constructor() {
        this.x = 0;
        this.y = 0;
    }

    public getArrowPosition() {
        return { x: this.x, y: this.y };
    }
    public setArrowPosition(x: number, y: number) {
        this.x = x;
        this.y = y;
    }
}

class Speed {
    speed: number = 0;
    setSpeed(s: number) {
        this.speed = s;
    }
    getSpeed() {
        return this.speed;
    }
}

class Size {
    size: number = 0;
    setSize(s: number) {
        this.size = s;
    }
    getSize() {
        return this.size;
    }
}
